.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #535353;
  color: #ffffff;
  text-align: center;
  form {
    box-shadow: #303030 15px 15px 60px;
    padding: 50px 30px;
 }    //border-radius: 10%

  span {
    margin: 20px;
    font-size: 11px; }

  label {
    margin: 20px; }


  input {
    border: none;
    border-bottom: solid 1px #06A4FF;
    font-size: 18px;
    margin: 20px;
    text-align: center;
    outline: none;
    background: #535353;
    color: #ffffff; } }

//&::placeholder
//  color: #5cd1f5


//form
